import React from "react";
import { Link } from "react-router-dom";
import { useSpring, animated } from "react-spring";

export default function NewsItem({
  author,
  date,
  viewed,
  id,
  img,
  title,
  category,
  body,
  visible,
}) {
  const stringDate = new Date(date);
  const month = stringDate.toLocaleString("default", { month: "long" });
  const day = stringDate.getDay();
  const year = stringDate.getFullYear();
  const props = useSpring({
    from: { opacity: 0, transform: "translateX(100px)" },
    opacity: visible ? 1 : 0,
    transform: visible ? "translateX(0)" : "translateX(100px)",
    delay: 250,
  });
  return (
    <animated.div style={props}>
      <div className="single-recent-blog-post">
        <div className="thumb">
          <img
            className="img-fluid"
            src={img || "/img/no-image.jpg"}
            alt="title"
          />
          <ul className="thumb-info">
            <li>
              <a href="#">
                <i className="ti-user" />
                {author}
              </a>
            </li>
            <li>
              <a href="#">
                <i className="ti-notepad" />
                {month} {day},{year}
              </a>
            </li>
          </ul>
        </div>
        <div className="details mt-20">
          <Link to={`/news/${id}`}>
            <h3>{title}</h3>
          </Link>
          <p className="tag-list-inline">Category: {category}</p>
          <p>{body != null && body.slice(0, 250)}</p>
          <Link className="button" to={`/news/${id}`}>
            Read More <i className="ti-arrow-right" />
          </Link>
        </div>
      </div>
    </animated.div>
  );
}
