import React from "react";
import NavbarSocialItem from "./NavbarSocialItem";

export default function NavbarList() {
  return (
    <ul className="nav navbar-nav navbar-right navbar-social">
      <NavbarSocialItem path="/youtube" social="youtube" />
      <NavbarSocialItem path="/facebook" social="facebook" />
      <NavbarSocialItem path="/instagram" social="instagram" />
    </ul>
  );
}
