import React from "react";

const Pagination = ({ goTo, current, total }) => {
  const pages = Math.ceil(total / 8);
  const back = () => {
    goTo((prev) => {
      if (prev > 1) {
        return prev - 1;
      }
    });
  };
  const next = () => {
    goTo((prev) => {
      if (prev < 8) {
        return prev + 1;
      }
    });
  };
  return (
    <>
      <nav className="blog-pagination justify-content-center d-flex">
        <ul className="pagination">
          <li className="page-item">
            <button onClick={back} className="page-link" aria-label="Previous">
              <span aria-hidden="true">
                <i className="ti-angle-left" />
              </span>
            </button>
          </li>
          {[...Array(pages)].slice(0, 5).map((n, index) => (
            <li
              key={index}
              className={`page-item ${index + 1 === current && "active"}`}
            >
              <button
                onClick={goTo.bind(null, index + 1)}
                className="page-link"
              >
                {index + 1}
              </button>
            </li>
          ))}
          <li className="page-item">
            <button onClick={next} className="page-link" aria-label="Next">
              <span aria-hidden="true">
                <i className="ti-angle-right" />
              </span>
            </button>
          </li>
        </ul>
      </nav>
    </>
  );
};

export default Pagination;
