import React from "react";
import { Link } from "react-router-dom";

export default function SliderItem({
  img,
  id,
  category,
  title,
  date,
  categoryId,
}) {
  const formatDate = new Date(date);
  return (
    <div className="card blog__slide text-center">
      <div className="blog__slide__img">
        <img
          style={style.img}
          className="card-img rounded-0"
          src={img || "img/no-image.jpg"}
          alt=""
        />
      </div>
      <div className="blog__slide__content">
        <Link className="blog__slide__label" to={`/category/${category._id}`}>
          {category}
        </Link>
        <h3>
          <Link to={`/news/${id}`}>{title}</Link>
        </h3>
        <p>
          {formatDate.getDate()}-{formatDate.getMonth()}-
          {formatDate.getFullYear()}
        </p>
      </div>
    </div>
  );
}

const style = {
  img: {
    objectFit: "cover",
    height: 200,
  },
};
