import React, { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import Sidebar from "../../components/Sidebar";
import Axios from "axios";
import Spinner from "../../components/Spinner";
import { set } from "mongoose";
import ReactHtmlParser from "react-html-parser";
import { createElement } from "react";

const News = () => {
  const { id } = useParams();

  const [post, setPost] = useState({});
  const [loading, setLoading] = useState(true);
  const formattedDate = new Date(post.date);

  useEffect(() => {
    fetchPost();
  }, [id]);

  const fetchPost = async () => {
    setLoading(true);
    const { data } = await Axios.get(`/news/getNewsById/${id}`);

    setPost(data);
    setLoading(false);
  };

  if (loading) return <Spinner />;

  return (
    <section className="blog-post-area section-margin">
      <div className="container">
        <div className="row">
          <div className="col-lg-8">
            <div className="main_blog_details">
              <img className="img-fluid" src={post.img} alt />
              <h4>{post.title}</h4>
              <div className="user_details">
                <div className="float-left">
                  <Link to={`category/${post.category.id}`}>
                    {post.category.title}
                  </Link>
                </div>
                <div className="float-right mt-sm-0 mt-3">
                  <div className="media">
                    <div className="media-body">
                      <h5>{post.author}</h5>
                      <p>{formattedDate.toLocaleDateString()}</p>
                    </div>
                    <div className="d-flex"></div>
                  </div>
                </div>
              </div>
              {ReactHtmlParser(post.body)}
              <div className="news_d_footer flex-column flex-sm-row">
                <div className="news_socail ml-sm-auto mt-sm-0 mt-2">
                  <a href="#">
                    <i className="fab fa-facebook-f" />
                  </a>
                  <a href="#">
                    <i className="fab fa-twitter" />
                  </a>
                  <a href="#">
                    <i className="fab fa-dribbble" />
                  </a>
                  <a href="#">
                    <i className="fab fa-behance" />
                  </a>
                </div>
              </div>
            </div>
          </div>
          {/* Start Blog Post Siddebar */}
          <Sidebar />
        </div>
        {/* End Blog Post Siddebar */}
      </div>
    </section>
  );
};

export default News;
