import React, { useState, useEffect, useRef, useContext } from "react";
import OwlCarousel from "react-owl-carousel";
import { store } from "../../context/MainContext";

import SliderItem from "./SliderItem";

export default function Slider() {
  const [slider] = useContext(store);
  return (
    <section>
      <div className="container">
        <OwlCarousel
          className="owl-carousel owl-theme blog-slider"
          loop
          margin={30}
          items={1}
          nav
          dots={false}
          smartSpeed={1500}
          responsiveClass
          autoplay={2500}
          navText={[
            "<div class='blog-slider__leftArrow'><img src='img/home/left-arrow.png'></div>",
            "<div class='blog-slider__rightArrow'><img src='img/home/right-arrow.png'></div>",
          ]}
          responsive={{
            0: {
              items: 1,
            },
            600: {
              items: 2,
            },
            1000: {
              items: 3,
            },
          }}
        >
          {slider.posts.map((article) => {
            // prettier-ignore

            return (
              <SliderItem
                // prettier-ignore
                img={article.img}
                id={article._id}
                key={article._id}
                category={article.category.title}
                categoryId={article.category.id}
                title={article.title}
                date={article.date}
              />
            );
          })}

          {/* <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem />
          <SliderItem /> */}
        </OwlCarousel>
      </div>
    </section>
  );
}
