import React, { useState, useEffect, Suspense } from "react";
import Axios from "axios";
import { Link } from "react-router-dom";
import moment from "moment";

const Sidebar = () => {
  const [news, setNews] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    const { data: fetchedNews } = await Axios.get(`/news/getPopularNews/1`);
    const { data: fetchedCategories } = await Axios.get(`/category`);
    console.log(news);

    setCategories(fetchedCategories);
    setNews(fetchedNews);
    setLoading(false);
  };
  if (loading) return null;
  return (
    <div className="col-lg-4 sidebar-widgets">
      <div className="widget-wrap">
        <div className="single-sidebar-widget newsletter-widget">
          <h4 className="single-sidebar-widget__title">Newsletter</h4>
          <div className="form-group mt-30">
            <div className="col-autos">
              <input
                type="text"
                className="form-control"
                id="inlineFormInputGroup"
                placeholder="Enter email"
              />
            </div>
          </div>
          <button className="bbtns d-block mt-20 w-100">Subcribe</button>
        </div>
        <div className="single-sidebar-widget post-category-widget">
          <h4 className="single-sidebar-widget__title">Catgory</h4>
          <ul className="cat-list mt-20">
            {categories.map((i) => (
              <li key={i._id}>
                <Link
                  to={`/category/${i._id}`}
                  className="d-flex justify-content-between"
                >
                  <p>{i.title}</p>
                </Link>
              </li>
            ))}
          </ul>
        </div>
        <div className="single-sidebar-widget popular-post-widget">
          <h4 className="single-sidebar-widget__title">Popular Post</h4>
          <div className="popular-post-list">
            {news.posts.map((i) => (
              <div key={i._id} className="single-post-list">
                <div className="thumb">
                  <img className="card-img rounded-0" src={i.img} alt="img" />
                  <ul className="thumb-info">
                    <li>
                      <a href="#">{i.author}</a>
                    </li>
                    <li>
                      <a href="#">{moment(i.date).format("MMMM Do YYYY")}</a>
                    </li>
                  </ul>
                </div>
                <div className="details mt-20">
                  <Link to={`/news/${i._id}`}>
                    <h6>{i.title}</h6>
                  </Link>
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className="single-sidebar-widget tag_cloud_widget">
          <h4 className="single-sidebar-widget__title">Popular Post</h4>
          <ul className="list">
            <li>
              <a href="#">project</a>
            </li>
            <li>
              <a href="#">love</a>
            </li>
            <li>
              <a href="#">technology</a>
            </li>
            <li>
              <a href="#">travel</a>
            </li>
            <li>
              <a href="#">software</a>
            </li>
            <li>
              <a href="#">life style</a>
            </li>
            <li>
              <a href="#">design</a>
            </li>
            <li>
              <a href="#">illustration</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
