import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Sidebar from "../../components/Sidebar";
import Spinner from "../../components/Spinner";
import Axios from "axios";
import { Link } from "react-router-dom";
import Pagination from "../../components/Pagination";
import { useTrail, animated } from "react-spring";
import striptags from "striptags";

function Category() {
  const { id } = useParams();
  const [news, setNews] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(true);

  const trail = useTrail(8, {
    from: { opacity: 0, transform: "translate(100px)" },
    opacity: 1,
    transform: "translate(0)",
  });

  useEffect(() => {
    console.log(trail);

    fetchData();
  }, [page, id]);

  const fetchData = async () => {
    let isMounted = true;
    setLoading(true);
    const { data } = await Axios.get(`/news/getNewsByCategory/${id}/${page}`);

    setNews(data);
    setLoading(false);
    return () => {
      isMounted = false;
    };
  };

  if (loading) return <Spinner />;

  return (
    <section className="blog-post-area section-margin">
      <div className="container">
        <div className="row">
          <div className="col-lg-8">
            <div className="row">
              {news.posts.map((i, index) => (
                <div key={i._id} className="col-md-6">
                  <animated.div style={trail[index]}>
                    <div className="single-recent-blog-post card-view">
                      <div className="thumb">
                        <img className="card-img rounded-0" src={i.img} alt />
                        <ul className="thumb-info">
                          <li>
                            <a href="#">
                              <i className="ti-user" />
                              {i.author}
                            </a>
                          </li>
                          <li>
                            <a href="#">
                              <i className="ti-themify-favicon" />2 Comments
                            </a>
                          </li>
                        </ul>
                      </div>
                      <div className="details mt-20">
                        <Link to={`/news/${i._id}`}>
                          <h3>{i.title}</h3>
                        </Link>
                        <p>
                          {i.body != null && striptags(i.body).slice(0, 250)}
                        </p>
                        <Link className="button" to={`/news/${i._id}`}>
                          Read More <i className="ti-arrow-right" />
                        </Link>
                      </div>
                    </div>
                  </animated.div>
                </div>
              ))}
            </div>

            <div className="row">
              <div className="col-lg-12">
                <Pagination goTo={setPage} current={page} total={news.count} />
              </div>
            </div>
          </div>
          <Sidebar />
        </div>
      </div>
    </section>
  );
}

export default Category;
