import React, { createContext, useState, useEffect } from "react";
import axios from "axios";
import Spinner from "../components/Spinner";
import { token } from "../config/config";

const initialState = {};
const store = createContext(initialState);
const { Provider } = store;

function MainContext({ children }) {
  const [slider, setSlider] = useState([]);
  const [recentPosts, setRecentPosts] = useState([]);
  const [rPostsPage, setRPostsPage] = useState(1);
  const [loading, setLoading] = useState(true);

  const fetchData = async () => {
    const { data: posts } = await axios.get(`/news/getRecentNews/1`);
    const { data: rPosts } = await axios.get(
      `/news/getRecentNews/${rPostsPage}`
    );

    setSlider(posts);
    setRecentPosts(rPosts);
    setLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (loading) {
    return <Spinner />;
  }
  return (
    <>
      <Provider value={[slider, recentPosts, rPostsPage, setRPostsPage]}>
        {children}
      </Provider>
    </>
  );
}

export { store, MainContext };
